$basecolor: #67C23A;
.indexbox {
    background: #F5F5F9;
    margin-top: 4rem;
    .searchbox {
        width: 100%;
        height: 4rem;
        background: $basecolor;
        color: #ffffff;
        display: flex;
        justify-content: space-around;
        align-items: center;
        font-size: 1.2rem;
        position: fixed;
        top: 0;
        z-index: 20000;
        a {
            color: #fff; }
        img {
            width: 2rem;
            height: 2rem; }
        .search {
            width: 70%;
            height: 2.5rem;
            background: #ffffff;
            border-radius: 40px;
            padding-left: 1rem;
            display: flex;
            align-items: center;
            font-size: 1.2rem;
            a {
                color: #ccc;
                img {
                    width: 1.5rem;
                    height: 1.5rem; } } } }
    .accountbox {
        list-style: none;
        margin: 0;
        padding: .5rem 1rem;
        display: flex;
        justify-content: space-around;
        align-items: center;
        color: rgb(136, 136, 136);
        background: #ffffff;
        li {
            text-align: center;
            padding-right: 1rem;
            &:not(:first-child) {
                border-right: 1px solid #eee; }
            &:last-child {
                border-right: none; }
            img {
                width: 4rem;
                height: 4rem;
                border-radius: 40px; } } } }




