.mybox {
    background: #f2f2f2;
    .avatarbox {
        padding: 1.5rem;
        text-align: center;
        background: url('../../../assets/images/my-bg.jpg') center/cover;
        img {
            width: 30%;
            height: 40%;
            border-radius: 100px;
            border-radius: 100px;
            border: 2px solid #67C23A; }
        p {
            line-height: 2.5rem;
            font-weight: bold;
            font-size: 1.4rem;
            color: #666; } }
    .myinfo {
        list-style: none;
        display: flex;
        justify-content: space-around;
        background: #f2f2f2;
        font-size: 1.3rem;
        margin: 0;
        padding: 1rem;
        color: #666666;
        li {
            display: flex;
            align-items: center;
            img {
                    width: 1.3rem;
                    height: 1.3rem; }
            a {
                color: #67C23A;
                font-size: 1.3rem;
                span {
                    margin-left: .5rem; } } } } }




